@import './reset.css';
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:wght@400;500;700&display=swap');

:root {
	/* Colors */
	--blackpure: #000;
	--black: #171718;
	--black-border: #26292d;
	--white: #fff;
	--purple: #5c62ec;

	--nav-border: var(--black-border);
}

.light {
	/* Header */
	--header-bg: var(--white);
	--header-text: var(--black);

	/* Nav */
	--nav-bg: var(--white);
	--nav-text: var(--black);

	/* Page */
	--page-bg: var(--white);
	--text-color: var(--black);

	/*Project cards*/
	--project-card-bg: var(--white);
	--project-card-text: var(--black);

	/* Footer */
	--footer-bg: var(--white);
	--footer-text: var(--black);

	background-color: var(--page-bg);
	color: var(--text-color);
	--accent: var(--purple);
	--title-1: var(--accent);
	--box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.2);
	background-image: url('../img/header-bg-light.png');
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-position: center;
}

.dark {
	/* Header */
	--header-bg: var(--black);
	--header-text: var(--white);

	/* Nav */
	--nav-bg: var(--black);
	--nav-text: var(--white);

	/* Page */
	--page-bg: var(--black);
	--text-color: var(--white);

	/*Project cards*/
	--project-card-bg: var(--black);
	--project-card-text: var(--white);

	/* Footer */
	--footer-bg: var(--black);
	--footer-text: var(--white);

	background-color: var(--page-bg);
	color: var(--text-color);
	--accent: var(--purple);
	--title-1: var(--accent);
	--box-shadow: 0px 5px 20px rgba(255, 255, 255, 0.2);
	background-image: url('../img/header-bg-dark.png');
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-position: center;
}

#root {
	min-height: 100vh;
}

::-moz-selection { /* Code for Firefox */
	color: var(--white);
	background: var(--accent);
}

::selection {
	color: var(--white);
	background: var(--accent);
}

html,
body {
	min-height: 100vh;
	font-family: 'DM Sans','Montserrat', sans-serif;
	letter-spacing: -0.5px;
	width: 100%;
	position: relative;
}

.App {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

.scrollbar
{
	height: 100vh;
	width: 100vw;
	overflow-y: auto;
}

.force-overflow
{
	min-height: 450px;
}

#custom-scroll::-webkit-scrollbar-track
{
	/*-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);*/
	border-radius: 10px;
	/*background-color: #F5F5F5;*/
}

#custom-scroll::-webkit-scrollbar
{
	width: 12px;
	/*background-color: #F5F5F5;*/
}

#custom-scroll::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	/*background-color: #555;*/
	background-color: var(--purple);
}

/* General */

.container {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1200px;
}

.none {
	display: none !important;
}

.section {
	padding: 70px 0;
}

.title-1 {
	margin-bottom: 60px;

	font-size: 60px;
	font-weight: 700;
	line-height: 1.3;
	color: var(--title-1);
	text-align: center;
}

.title-2 {
	margin-bottom: 20px;

	font-size: 40px;
	font-weight: 700;
	line-height: 1.3;
}

/* Nav */



/* Dark mode btn */

.dark-mode-btn {
	position: relative;
	display: flex;
	align-items: center;
	width: 51px;
	height: 26px;
	border-radius: 50px;
	background-color: #272727;
	cursor: pointer;
	border: none;
	padding: 0;
	transition: background-color 0.3s ease;
}

.dark-mode-btn::before {
	content: '';
	position: absolute;
	top: 1px;
	left: 1px;
	width: 24px;
	height: 24px;
	border-radius: 50%;
	background-color: var(--white);
	transition: left 0.3s ease;
}

.dark-mode-btn--active::before {
	left: calc(100% - 25px); /* Move the pseudo-element to the right */
}

.dark-mode-btn__icon {
	position: absolute;
	left: 5px;
	width: 16px;
	height: 16px;
	transition: left 0.3s ease;
}

.dark-mode-btn--active .dark-mode-btn__icon {
	left: calc(100% - 21px); /* Move the icon to the right */
}

/* Header */

.header {
	padding: 40px 0;
	min-height: 695px;

	display: flex;
	justify-content: center;
	align-items: center;

	/*color: var(--header-text);*/
	text-align: center;
}



/* Btn */

.btn {
	display: inline-block;
	height: 48px;
	padding: 12px 28px;

	border-radius: 5px;
	background-color: var(--accent);
	color: var(--white);
	letter-spacing: 1px;
	font-size: 16px;
	font-weight: 500;

	transition: opacity 0.2s ease-in;
}

.btn:hover {
	opacity: 0.8;
}

.btn:active {
	position: relative;
	top: 1px;
}


/* Projects  */

.projects {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	column-gap: 30px;
	row-gap: 30px;
}



/* Project details */

.project-details {
	margin: 0 auto;
	max-width: 865px;

	display: flex;
	flex-direction: column;
	align-items: center;

	text-align: center;
}

.project-details__cover {
	max-width: 100%;
	margin-bottom: 40px;

	box-shadow: var(--box-shadow);
	border-radius: 10px;
}

.project-details__desc {
	margin-bottom: 30px;

	font-weight: 700;
	font-size: 24px;
	line-height: 1.3;
}

/* Content list */

.content-list {
	margin: 0 auto;
	max-width: 570px;
	display: flex;
	flex-direction: column;
	align-items: center;
	row-gap: 40px;
	text-align: center;
}

.content-list a {
	color: var(--accent);
}

.content-list__item {
	font-size: 18px;
	line-height: 1.5;
}

.content-list__item p+p {
	margin-top: 0.5em;
}

/* Footer */

/* Social Icons */
.social__item {
	display: inline-block;
	transition: transform 0.3s ease;
	/*transition: transform 0.3s ease, filter 0.3s ease, box-shadow 0.3s ease;*/
	/* Ensure the icons don't shrink or overflow on hover */
	overflow: hidden;
}

.social__item:hover {
	transform: scale(0.9);
	/*filter: blur(1px);*/
	/*box-shadow: 1px 1px 16px var(--purple);*/
}

@media (max-width: 890px) {
	/* Header  */

	.header {
		min-height: unset;
	}

	.header__title {
		font-size: 30px;
	}

	.header__title strong {
		font-size: 40px;
	}

	/* Nav */

	.nav-row {
		justify-content: space-between;
	}

	.dark-mode-btn {
		order: 0;
	}

	/* General */

	/*.section {*/
	/*	padding: 40px 0;*/
	/*}*/

	.title-1 {
		margin-bottom: 30px;
		font-size: 40px;
	}

	.title-2 {
		margin-bottom: 10px;
		font-size: 30px;
	}

	/* Projects */

	.project__title {
		font-size: 22px;
	}

	/* Project page */

	.project-details__desc {
		margin-bottom: 20px;
		font-size: 22px;
	}

	/* Content List */

	.content-list {
		row-gap: 20px;
	}

	.content-list__item {
		font-size: 16px;
	}

	/* Footer */

	.footer {
		padding: 40px 0 30px;
	}

	.footer__wrapper {
		row-gap: 20px;
	}

	/* Social */

	.social {
		column-gap: 20px;
	}

	.social__item {
		width: 36px;
	}
}