@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
/* Reset and base styles  */
* {
	padding: 0;
	margin: 0;
	border: none;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

:focus,
:active {
	/*outline: none;*/
}

a:focus,
a:active {
	/* outline: none;*/
}

/* Links */

a, a:link, a:visited  {
    /* color: inherit; */
    text-decoration: none;
    /* display: inline-block; */
}

a:hover  {
    /* color: inherit; */
    text-decoration: none;
}

/* Common */

aside, nav, footer, header, section, main {
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
    font-size: inherit;
	font-weight: inherit;
}

ul, ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

img, svg {
	max-width: 100%;
	height: auto;
}

address {
  font-style: normal;
}

/* Form */

input, textarea, button, select {
	font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
}

input::-ms-clear {
	display: none;
}

button, input[type="submit"] {
    display: inline-block;
    box-shadow: none;
    /*background-color: transparent;*/
    background: none;
    cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
    outline: none;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

label {
	cursor: pointer;
}

legend {
	display: block;
}

:root {
	/* Colors */
	--blackpure: #000;
	--black: #171718;
	--black-border: #26292d;
	--white: #fff;
	--purple: #5c62ec;

	--nav-border: var(--black-border);
}

.light {
	/* Header */
	--header-bg: var(--white);
	--header-text: var(--black);

	/* Nav */
	--nav-bg: var(--white);
	--nav-text: var(--black);

	/* Page */
	--page-bg: var(--white);
	--text-color: var(--black);

	/*Project cards*/
	--project-card-bg: var(--white);
	--project-card-text: var(--black);

	/* Footer */
	--footer-bg: var(--white);
	--footer-text: var(--black);

	background-color: var(--page-bg);
	color: var(--text-color);
	--accent: var(--purple);
	--title-1: var(--accent);
	--box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.2);
	background-image: url(/portfolio/static/media/header-bg-light.4c03563b.png);
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-position: center;
}

.dark {
	/* Header */
	--header-bg: var(--black);
	--header-text: var(--white);

	/* Nav */
	--nav-bg: var(--black);
	--nav-text: var(--white);

	/* Page */
	--page-bg: var(--black);
	--text-color: var(--white);

	/*Project cards*/
	--project-card-bg: var(--black);
	--project-card-text: var(--white);

	/* Footer */
	--footer-bg: var(--black);
	--footer-text: var(--white);

	background-color: var(--page-bg);
	color: var(--text-color);
	--accent: var(--purple);
	--title-1: var(--accent);
	--box-shadow: 0px 5px 20px rgba(255, 255, 255, 0.2);
	background-image: url(/portfolio/static/media/header-bg-dark.6f3e1993.png);
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-position: center;
}

#root {
	min-height: 100vh;
}

