/* Nav */



/* Dark mode btn */

.dark-mode-btn {
	position: relative;
	display: flex;
	align-items: center;
	width: 51px;
	height: 26px;
	border-radius: 50px;
	background-color: #272727;
	cursor: pointer;
	border: none;
	padding: 0;
	transition: background-color 0.3s ease;
}

.dark-mode-btn::before {
	content: '';
	position: absolute;
	top: 1px;
	left: 1px;
	width: 24px;
	height: 24px;
	border-radius: 50%;
	background-color: var(--white);
	transition: left 0.3s ease;
}

.dark-mode-btn--active::before {
	left: calc(100% - 25px); /* Move the pseudo-element to the right */
}

.dark-mode-btn__icon {
	position: absolute;
	left: 5px;
	width: 16px;
	height: 16px;
	transition: left 0.3s ease;
}

.dark-mode-btn--active .dark-mode-btn__icon {
	left: calc(100% - 21px); /* Move the icon to the right */
}

/* Header */

.header {
	padding: 40px 0;
	min-height: 695px;

	display: flex;
	justify-content: center;
	align-items: center;

	/*color: var(--header-text);*/
	text-align: center;
}



/* Btn */

.btn {
	display: inline-block;
	height: 48px;
	padding: 12px 28px;

	border-radius: 5px;
	background-color: var(--accent);
	color: var(--white);
	letter-spacing: 1px;
	font-size: 16px;
	font-weight: 500;

	transition: opacity 0.2s ease-in;
}

.btn:hover {
	opacity: 0.8;
}

.btn:active {
	position: relative;
	top: 1px;
}


/* Projects  */

.projects {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	grid-column-gap: 30px;
	column-gap: 30px;
	grid-row-gap: 30px;
	row-gap: 30px;
}



/* Project details */

.project-details {
	margin: 0 auto;
	max-width: 865px;

	display: flex;
	flex-direction: column;
	align-items: center;

	text-align: center;
}

.project-details__cover {
	max-width: 100%;
	margin-bottom: 40px;

	box-shadow: var(--box-shadow);
	border-radius: 10px;
}

.project-details__desc {
	margin-bottom: 30px;

	font-weight: 700;
	font-size: 24px;
	line-height: 1.3;
}

/* Content list */

.content-list {
	margin: 0 auto;
	max-width: 570px;
	display: flex;
	flex-direction: column;
	align-items: center;
	grid-row-gap: 40px;
	row-gap: 40px;
	text-align: center;
}

.content-list a {
	color: var(--accent);
}

.content-list__item {
	font-size: 18px;
	line-height: 1.5;
}

.content-list__item p+p {
	margin-top: 0.5em;
}

/* Footer */

/* Social Icons */
.social__item {
	display: inline-block;
	transition: transform 0.3s ease;
	/*transition: transform 0.3s ease, filter 0.3s ease, box-shadow 0.3s ease;*/
	/* Ensure the icons don't shrink or overflow on hover */
	overflow: hidden;
}

.social__item:hover {
	transform: scale(0.9);
	/*filter: blur(1px);*/
	/*box-shadow: 1px 1px 16px var(--purple);*/
}

@media (max-width: 890px) {
	/* Header  */

	.header {
		min-height: unset;
	}

	.header__title {
		font-size: 30px;
	}

	.header__title strong {
		font-size: 40px;
	}

	/* Nav */

	.nav-row {
		justify-content: space-between;
	}

	.dark-mode-btn {
		order: 0;
	}

	/* General */

	/*.section {*/
	/*	padding: 40px 0;*/
	/*}*/

	.title-1 {
		margin-bottom: 30px;
		font-size: 40px;
	}

	.title-2 {
