.list {
    position: relative;
}
.list h2 {
    color: var(--header-text);
    font-weight: 700;
    letter-spacing: 1px;
    margin-bottom: 10px;
}
.list ul {
    position: relative;
}
.list ul li {
    position: relative;
    left: 0;
    color: var(--header-text);
    list-style: none;
    margin: 4px 0;
    border-left: 2px solid var(--purple);
    transition: 0.5s;
    cursor: pointer;
}
.list ul li:hover {
    left: 10px;
}
.list ul li span {
    position: relative;
    padding: 8px 8px 8px 12px;
    display: inline-block;
    z-index: 1;
    transition: 0.5s;
}
.list ul li:hover span {
    filter: invert(100%);
}
.list ul li:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: var(--purple);
    transform: scaleX(0);
    transform-origin: left;
    transition: 0.5s;
}
.list ul li:hover:before {
    transform: scaleX(1);
}
.nav {
	top: 0;
	position: -webkit-sticky;
	position: sticky;
	z-index: 9999;
	padding: 20px 0;
	background-color: var(--nav-bg);
	border-bottom: 1px solid var(--nav-border);
	color: var(--nav-text);
	letter-spacing: normal;
	transition: background-color 0.3s ease-in-out;
}

.nav-row {
	display: flex;
	justify-content: space-between; /* Adjusted to space-between for better layout */
	align-items: center;
	grid-column-gap: 30px;
	column-gap: 30px;
	grid-row-gap: 20px;
	row-gap: 20px;
	flex-wrap: wrap;
}

.logo {
	color: var(--nav-text);
	font-size: 24px;
	font-family: 'Poppins', sans-serif;
}

.logo strong {
	font-weight: 700;
}

.burger-icon {
	display: none;
	font-size: 2rem;
	cursor: pointer;
	color: var(--nav-text);
	transition: color 0.3s ease-in-out;
}

.nav-list {
	display: flex;
	flex-wrap: wrap;
	grid-row-gap: 10px;
	row-gap: 10px;
	align-items: center;
	grid-column-gap: 40px;
	column-gap: 40px;
	font-size: 16px;
	font-weight: 500;
	font-family: 'Poppins', sans-serif;
}

.nav-list__link {
	margin-left: 15px;
	color: var(--nav-text);
	transition: opacity 0.2s ease-in, color 0.2s ease-in;
	position: relative; /* Needed for the underline effect */
}

.nav-list__link:hover {
	opacity: 0.8;
}

.nav-list__link--active {
	color: var(--accent);
	/*font-weight: bold;*/
}

.nav-list__link--active::after,
.nav-list__link::after {
	content: '';
	position: absolute;
	left: 0;
	bottom: -2px;
	width: 100%;
	height: 2px;
	background-color: var(--accent);
	transform: scaleX(0);
	transform-origin: bottom right;
	transition: transform 0.2s ease-in-out;
}

.nav-list__link--active::after {
	transform: scaleX(1);
	transform-origin: bottom left;
}

.nav-list__link:hover::after {
	transform: scaleX(1);
	transform-origin: bottom left;
}

/* Media query for mobile devices */
@media (max-width: 890px) {
	.burger-icon {
		display: block;
	}

	.nav-list {
		display: flex;
		flex-direction: column;
		position: fixed;
		top: 0;
		right: 0;
		height: 100vh;
		width: 100%;
		max-width: 250px;
		background: var(--nav-bg);
		padding-top: 60px;
		box-shadow: -2px 0 5px rgba(128, 128, 128, 0.5);
		transform: translateX(100%);
		transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
		opacity: 0; /* Start hidden */
		pointer-events: none; /* Disable interactions when hidden */
		overflow-y: auto; /* Ensure scroll if content overflows */
	}

	.nav-list.open {
		transform: translateX(0); /* Slide into view */
		opacity: 1; /* Fully visible */
		pointer-events: auto; /* Enable interactions */
	}

	.nav-list__item {
		margin: 0;
		opacity: 0; /* Start hidden */
		transform: translateX(50px); /* Start slightly shifted to the right */
		transition: opacity 0.2s ease-in-out, transform 0.2s ease-in-out; /* Smooth transition */
	}

	.nav-list.open .nav-list__item {
		opacity: 1; /* Fade in */
		transform: translateX(0); /* Slide into place */
	}

	.nav-list__link {
		margin: 0;
		/*padding: 1rem;*/
		text-align: right;
	}

	.nav-list__item:last-child {
		margin-top: 1rem;
		text-align: right;
	}
}
