.list {
    position: relative;
}
.list h2 {
    color: var(--header-text);
    font-weight: 700;
    letter-spacing: 1px;
    margin-bottom: 10px;
}
.list ul {
    position: relative;
}
.list ul li {
    position: relative;
    left: 0;
    color: var(--header-text);
    list-style: none;
    margin: 4px 0;
    border-left: 2px solid var(--purple);
    transition: 0.5s;
    cursor: pointer;
}
.list ul li:hover {
    left: 10px;
}
.list ul li span {
    position: relative;
    padding: 8px 8px 8px 12px;
    display: inline-block;
    z-index: 1;
    transition: 0.5s;
}
.list ul li:hover span {
    filter: invert(100%);
}
.list ul li:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: var(--purple);
    transform: scaleX(0);
    transform-origin: left;
    transition: 0.5s;
}
.list ul li:hover:before {
    transform: scaleX(1);
}